.footer-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    padding-top: 28px;
    padding-bottom: 28px;
    text-align: center;

    font-weight: 400;
    font-size: 14px;
    line-height: 1.14;

    color: #545454;

    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 1.19;
        padding-top: 30px;
        padding-bottom: 30px;
    }
    @media screen and (min-width: 1280px) {
        padding-top: 29px;
        padding-bottom: 29px;
    }

    &__item:first-child {
        margin-bottom: 12px;

        @media screen and (min-width: 768px) {
            margin-bottom: 0px;
        }
    }

}


.link-underline {
    text-decoration: underline;
    color: #545454;
}

